<template>

  <div class="SingleProduct_wrapper">
    <div class="SingleProduct_top">
        <div class="content">
            <div class="left_area">
                <div class="main_img">
                    <img :src="mainProduct.src" >
                </div>
                <div class="detail_img">
                    <img :src="mainProduct.small_img_01" @click="changeImg(mainProduct.small_img_01)">
                    <img :src="mainProduct.small_img_02" @click="changeImg(mainProduct.small_img_02)">
                    <img :src="mainProduct.small_img_03" @click="changeImg(mainProduct.small_img_03)">
                </div>
            </div>
            <div class="right_area">
                <h5>首頁 / 文創商品 </h5>
                <h2>{{ mainProduct.title }}</h2>
                <h3>{{ mainProduct.price }}</h3>
                <p v-html="mainProduct.content">
                </p>
                <div class="choose_quantity">
                    <div class="quantity_left">
                        <div class="reduce" @click="reduce">-</div>
                        <h6>{{quantity}}</h6>
                        <div class="add" @click="add">+</div>
                    </div>
                    <div class="quantity_right">
                        <p>加入購物車</p>
                    </div>
                </div>
                <div class="buy_now">
                    <h2>立即購買</h2>
                </div>
            </div>
        </div>
    </div>

    <div class="interval">
      <div class="rectangle"></div>
      <p>你可能喜歡的商品</p>
    </div>

    <div class="Commodity_main">
      <ul>
        <li v-for="item in items" :key="item.index">
          <img :src="item.src">
          <h4>{{item.title}}</h4>
          <p>{{item.price}}</p>
        </li>
      </ul>
    </div>

    <footer>
      <div>
        <img src="../assets/旅人logo_2.png">
        <p>
          © 2022 TRAVELER 旅人<br>
          訂房專線：0912-345-678<br>
          地址：263新北市永和區新南路一段58號
        </p>
      </div>
    </footer>
  </div>

</template>

<script>
export default {
  name: "SingleProduct",

  components: {
  },

  data: () => ({
    items: [
            {
        "src": "img/commodity_items_01.jpg",
        "title":"旅人芬芳洗髮乳",
        "price":"NT$ 349",
        "id":1,
        "link" :"http://localhost:8080/#/SingleProduct?prods=1"
      },
      {
        "src": "img/commodity_items_02.jpg",
        "title":"旅人抹茶沐浴乳",
        "price":"NT$ 299",
        "id":2,
        "link" :"http://localhost:8080/#/SingleProduct?prods=2"
      },
      {
        "src": "img/commodity_items_03.jpg",
        "title":"旅人晶透乳液",
        "price":"NT$ 449",
        "id":3
      },
      {
        "src": "img/commodity_items_04.jpg",
        "title":"旅人潔淨洗面乳",
        "price":"NT$ 299",
        "id":4
      },
      {
        "src": "img/commodity_items_05.jpg",
        "title":"經典白浴袍(女款)",
        "price":"NT$ 699",
        "id":5
      },
      {
        "src": "img/commodity_items_06.jpg",
        "title":"旅人經典浴巾",
        "price":"NT$ 249",
        "id":6
      }
    ],
    mainProduct:[],
    // obj_list:0,
    quantity: 1,
  }),
  methods: {
    changeImg(src){ // 切換步驟
        // console.log(e.target.getAttribute("src"));
        this.mainProduct.src = src
        // console.log(src);
    },
    reduce(){
      if(this.quantity > 1){
          this.quantity--
      }
    },
    add(){
          this.quantity++
    },
  },
  mounted(){
    console.log(this.$route.params.productId);
  },
   created(){
    // 如果把data裡面的items裡的資料清空,如這樣items: [] 就可以用下面ajax的方式接收資料
    // this.$axios.get('http://localhost:8086/productData')
    // .then((res) => {
    //   this.items = res.data
    // })
    this.$axios.get('http://localhost:8086/productDetailData')
    .then((res) => {
        // console.log(res.data);
        res.data.forEach(element => {
            if(element.id == this.$route.params.productId){
                this.mainProduct = element
            }
        });
    })
  },
};
</script>

