            }
            p{
                font-size: 16px;
                margin-top: 15px;
                line-height: 1.6;
            }
            .choose_quantity{
                display: flex;
                text-align: center;
                margin-top: 30px;
                .quantity_left{
                    display: flex;
                    justify-content: space-between;
