                          cursor: pointer;
                        }
                    }
                    h6{

                    }
                    .add{
                        height: 30px;
                        width: 30px;
                        border: 1px solid #494949;
                        &:hover{
                          cursor: pointer;
                        }
                    }
                }
                .quantity_right{
                    width: 105px;
                    height: 30px;
                    background-color: #494949;
                    border: 1px solid #494949;
                    margin-left: 30px;
                    p{
                        font-size: 1px;
                        color: rgb(255, 255, 255);
