                }
            }
        }
    }


}

.Commodity_main{
  width: 1200px;
  margin: 0 auto;
  ul{
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      
    li{
      margin-top: 50px;
      list-style: none;
      text-align: center;
      h4{
        font-size: 24px;
        margin-top: 20px;
      }
      p{
