    getTwoRoom(number){
      let Two = []
      // console.log(this.items);
      this.items.forEach(element => {
        // forEach為專門處理陣列的迴圈方式
        // 這裡的element和上面的item一樣
        // console.log(element);
        if(element.type == number){
          Two.push(element)
        }
      });
      // console.log(Two);
      this.itemsView = Two
      
      if(number == 2){
        this.index = 2
      }
      if(number == 4){
        this.index = 4
      }
      // --- 上面兩個if的寫法也可以寫成以下三元運算式的寫法 ---
